import { createStore } from 'vuex'

export default createStore({
  state: {
    server_cost: 0,
    man_cost: 0,
    summary_cost: 0,
  },
  getters: {
    get_server_cost(state) {
			return state.server_cost;
		},
    get_man_cost(state) {
			return state.man_cost;
		},
    get_sum_cost(state) {
			return parseInt(state.server_cost)+parseInt(state.man_cost);
		},
  },
  mutations: {
    set_server_cost(state, value) {
			state.server_cost = parseInt(value);
		},
    set_man_cost(state, value) {
			state.man_cost = parseInt(value);
		},
  },
  actions: {
  },
  modules: {
  }
})
