import { createRouter, createWebHashHistory } from 'vue-router'
import HomeView from '../views/home.vue'

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/calculate_cost',
    name: 'calculate_cost',
    component: function () {
      return import('../views/calculate_cost.vue')
    }
  },
  {
    path: '/calculate_server',
    name: 'calculate_server',
    component: function () {
      return import('../views/calculate_server.vue')
    }
  },
  {
    path: '/summary',
    name: 'summary',
    component: function () {
      return import('../views/summary.vue')
    }
  }
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

export default router
